:root {
    --primary: #7c587f;
  --secondary: #4c3f77;
  --tertiary: #B12704;
  --light: #a4bcbc;
  --dark: #051126;
  --contrast: #007f97;
}

* {
    background-color: var(--primary);
}

form {
    margin: 100px;
}

input {
    background-color: white;
}

textarea {
    background-color: white;
}

.headerNav {
    background-color: var(--secondary);
}

.navContainer {
    background-color: var(--secondary);
}

.myName h1{
    color: var(--dark);
    padding: 10px;
    background-color: var(--secondary);
    border-bottom: solid 3px var(--secondary);

}

.navLinks ul {
    font-size: large;
    padding: 10px;
    background-color: var(--secondary);
    border-bottom: solid 6px var(--secondary);
}

.nav-item {
    color: var(--dark);
    background-color: var(--secondary);
    margin: auto;
}

.cardContainer {
    border: solid 5px var(--light);
}

.footImg {
    height: 50px;
    width: 50px;
}